<template>
  <div>
    <h1>hello 2</h1>
    <v-data-table
      :headers="headers"
      :items="items"
      class="elevation-1"
      @click:row="onRowClick"
    >
      <template v-slot:item="{ item, index }">
        <tr :class="{ 'highlight-row': index === 1 }">
        
       
              <th class="text-left">
                Name
              </th>
              <th class="text-left">
                Calories
              </th>
            </tr>
         
        
        <tr :class="{ 'highlight-row': index === 2 }">
          <td v-for="header in headers" :key="header.value">
            <span v-if="!item.edit" @click="editField(item)">
              {{ item[header.value] }}
            </span>
            <input
              v-else
              v-model.lazy="item[header.value]"
            />
          </td>
        </tr>
      </template>
    </v-data-table>
  </div>
</template>

<script>
import { reactive } from 'vue';

export default {
  name: "TableComponent",
  setup() {
    const headers = [
      { text: 'Column 1', value: 'col1' },
      { text: 'Column 2', value: 'col2' }
    ];
    const items = reactive([
      { col1: 'Hello', col2: 'World', edit: false },
      { col1: 'vue-table', col2: 'rocks', edit: false },
      { col1: 'whatever', col2: 'you want', edit: false },
      { col1: 'row 4', col2: 'data', edit: false },
      { col1: 'row 5', col2: 'data', edit: false },
      { col1: 'row 6', col2: 'data', edit: false },
      { col1: 'row 7', col2: 'data', edit: false },
      { col1: 'row 8', col2: 'data', edit: false },
      { col1: 'row 9', col2: 'data', edit: false },
      { col1: 'row 10', col2: 'data', edit: false }
    ]);

    const editField = (item) => {
      item.edit = true;
    };

    const updateField = (item, field, value) => {
      item[field] = value;
      item.edit = false;
    };

    return {
      headers,
      items,
      editField,
      updateField
    };
  }
};
</script>

<style scoped>
.highlight-row {
  background-color: lightblue;
}

.v-data-table input {
  width: 100%;
  box-sizing: border-box;
}
</style>
